import React, { Component } from 'react';
import { connect } from 'react-redux';

import Header from './../header';
import Nav from './../nav';
import Banner from './../banner';
import IndexWeather from './../indexWeather';
import IndexInfo from './../indexInfo';
import ToTop from './../toTop';
import s from './Home';

import { indexNewsPost } from '../../actions/indexNews';

class Index extends Component {
    componentWillMount() {
        const { dispatch } = this.props;
        dispatch(indexNewsPost());
    }
    render() {
        return (
            <div className={s.box}>
                <Header data={data.data.forecast} />
                <Nav router={data.route.path} />
                <Banner />
                <IndexWeather />
                <IndexInfo data={data.data.indexNews} />
                <ToTop />
            </div>
        );
    }
}

const mapStateToProps = (state) => {
    const { indexNews, forecast } = state;

    return { indexNews, forecast };
};

export default connect(mapStateToProps)(Index);




